import React, { Component } from 'react';
import PaginationContainer from './PaginationContainer';
import ProductsListTable from './ProductsListTable';
import ProductDetail from './ProductDetail';
import FullScreenLoader from './FullScreenLoader';

class ProductsListView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      page: 1,
    };
    this.loadPage(this.state.page)
  }

  loadPage = (page) => {
    fetch(`https://api.software.madkting.com/shops/76/products/?page=${page}&page_size=30`, {
      headers: new Headers({
        Authorization: 'Token 599d4be34f2cf59df13ebb27e9852570bc0684d2',
      }),
    })
      .then(async (res) => {
        const productsData = await res.json();
        this.setState({
          loading: false,
          productsData,
          currentPage: res.headers.get('x-pagination-current-page'),
          pageCount: res.headers.get('x-pagination-page-count'),
          perPage: res.headers.get('x-pagination-per-page'),
          totalCount: res.headers.get('x-pagination-total-count'),
        })
      });
  }
    
  showProductDetail = (product) => {
    this.setState({ productDetail: product });
  }
  closeProductDetail = () => {
    this.setState({ productDetail: null });
  }
  
  setPage = (page) => {
    this.setState({ page: page, loading: true });
    this.loadPage(page)
  }
  
  render() {
    const {
      loading,
      currentPage,
      perPage,
      totalCount,
      productsData,
      productDetail,
    } = this.state;
    return (
      <>
        {loading ? (
         <FullScreenLoader />
        ) : (
          <div> 
            {productDetail && 
              <ProductDetail 
                product={productDetail} 
                closeProductDetail={this.closeProductDetail} 
              />
            }
            <ProductsListTable 
              productsData={productsData} 
              showProductDetail={this.showProductDetail}
            />
            <PaginationContainer
              currentPage={currentPage}
              pageCount={this.state.productsData.length}
              perPage={perPage}
              totalCount={totalCount}
              page={this.state.page}
              setPage={this.setPage}
            />
          </div>
        )}
      </>
    );
  }
}
export default ProductsListView;
