import React from 'react';
import Pagination from 'react-bootstrap/Pagination';

function PaginationContainer(props) {
  const pageCount = parseInt(props.pageCount);
  const totalCount = parseInt(props.totalCount);
  const perPage = parseInt(props.perPage);
  const currentPage = parseInt(props.currentPage);
  const lastPage = Math.ceil(totalCount / perPage);
  const pagesButtons = [];
  let from = currentPage <= 4 ? 2 : currentPage - 2;
  let to = currentPage <= 4 ? 4 : currentPage + 2;

  if (currentPage >= lastPage - 3) {
    from = lastPage - 3;
    to = lastPage - 1;
  }

  for (let j = from; j <= to; j += 1) {
    pagesButtons.push(
      <Pagination.Item
        key={j}
        {...(currentPage !== j ? { onClick: () => props.setPage(j) } : { active: true })}
      >
        {j}
      </Pagination.Item>,
    );
  }

  return (
    <div className="pagination-container">
      <Pagination>
        {currentPage > 1
            && (
            <Pagination.Prev onClick={() => props.setPage(currentPage - 1)}>
            Anterior
            </Pagination.Prev>
            )}
        <Pagination.Item
          {...(currentPage !== 1
            ? { onClick: () => props.setPage(1) }
            : { active: true })}
        >
          {1}
        </Pagination.Item>
        {currentPage > 4 && <Pagination.Ellipsis />}
        {pagesButtons}
        {(currentPage < lastPage - 3) && <Pagination.Ellipsis />}
        <Pagination.Item
          {...(currentPage !== lastPage
            ? { onClick: () => props.setPage(lastPage) }
            : { active: true })}
        >
          {lastPage}
        </Pagination.Item>
        {currentPage < lastPage
          && (
            <Pagination.Next onClick={() => props.setPage(currentPage + 1)}>
            Siguiente
            </Pagination.Next>)}
      </Pagination>
      <div>
        {`Mostrando del ${(perPage * currentPage) - perPage + 1} al
          ${((perPage * (currentPage - 1)) + pageCount)} de ${totalCount} resultados`}
      </div>
    </div>
  );
}

export default PaginationContainer;
