import React from 'react';
import Table from 'react-bootstrap/Table';

function ProductsListTable(props) {
  const products = props.productsData.map((item, key) => (
    <tr key={key} onClick={() => props.showProductDetail(item)}>
      <td>
        {item.images && (
        <div
          className="product-thumbnail products-list-table"
          style={{
            backgroundImage: `url(${item.images[0] ? item.images[0].url : 'https://via.placeholder.com/150'
            })`,
          }}
        />
        )}
      </td>
      <td>{item.sku}</td>
      <td>{item.name}</td>
      <td>{item.stock}</td>
      <td>{item.price}</td>
      <td>{item.brand}</td>
    </tr>
  ));
  return (
    <Table className="products-list-table" variant="light" striped bordered hover>
      <thead>
        <tr>
          <th></th>
          <th>SKU</th>
          <th>Nombre</th>
          <th>Stock</th>
          <th>Precio</th>
          <th>Marca</th>
        </tr>
      </thead>
      <tbody>
        {products}
      </tbody>
    </Table>
  );
}

export default ProductsListTable;
