import React from 'react';
import {
  Card,
  Table,
  Carousel,
  Modal,
} from 'react-bootstrap';

function ProductDetail(props) {
  const { product, closeProductDetail } = props;
  const productImages = product.images.map((item, key) => (
      <Carousel.Item key={key}>
        <div
          className="product-thumbnail product-detail"
          style={{
            backgroundImage: `url(${item.url})`,
          }}
        />
      </Carousel.Item>
  ));

  return (
    <Modal show onHide={closeProductDetail}>
      <Modal.Header closeButton />
      <Modal.Body>
        <Card>
          <Card.Header>
            <Card.Title>{product.name}</Card.Title>
          </Card.Header>
          <Card.Body>
            <Carousel className="product-detail-carousel">
              {productImages}
            </Carousel>
            <br />
            <Table bordered className="product-detail-table">
              <tbody>
                <tr>
                  <td>
                    <strong>SKU</strong>
                  </td>
                  <td>{product.sku}</td>
                </tr>
                <tr>
                  <td>
                    <strong>Precio</strong>
                  </td>
                  <td>{product.price}</td>
                </tr>
                <tr>
                  <td>
                    <strong>Marca</strong>
                  </td>
                  <td>{product.brand}</td>
                </tr>
                <tr>
                  <td>
                    <strong>Peso</strong>
                  </td>
                  <td>{`${product.weight} ${product.weight_unit}`}</td>
                </tr>
                <tr>
                  <td>
                    <strong>Dimensiones</strong>
                  </td>
                  <td>
                    {
                    `${product.shipping_width} ${product.dimensions_unit} x
                     ${product.shipping_height} ${product.dimensions_unit} x
                     ${product.shipping_height} ${product.dimensions_unit}`
                    }
                  </td>
                </tr>
              </tbody>
            </Table>
            {/* <Card.Text dangerouslySetInnerHTML={{ __html: product.description_html }} /> */}
          </Card.Body>
        </Card>
      </Modal.Body>
    </Modal>
  );
}

export default ProductDetail;
